@use "../config/g_prefix" as g;
@use "../config/typography" as typography;

html body {
    font-size: var(--bnds-g-font-size-14);
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
    display: block;
}


h1,
h2,
h3,
h4,
h5,
h6,
.#{g.$prefix-cls}-h1,
.#{g.$prefix-cls}-h2,
.#{g.$prefix-cls}-h3,
.#{g.$prefix-cls}-h4,
.#{g.$prefix-cls}-h5,
.#{g.$prefix-cls}-h6 {
    margin: var(--bnds-g-sizing-12) 0;
    padding: 0;
    line-height: var(--bnds-g-line-height-1);
    font-weight: var(--bnds-g-font-weight-semibold);
}


@each $key, $value in typography.$headings {

    #{$key},
    .#{g.$prefix-cls}-#{$key} {
        font-size: var(#{g.$prefix-font}-#{$value});
    }
}

.#{g.$prefix-cls}-strong,
b,
.#{g.$prefix-cls}-b,
strong,
b {
    font-weight: var(--bnds-g-font-weight-semibold);
}

ul,
ol {
    margin: var(--bnds-g-sizing-12) 0 var(--bnds-g-sizing-12) var(--bnds-g-sizing-24);
    padding: 0;
}

a,
.#{g.$prefix-cls}-a {
    color: var(--bnds-g-color-blue-50);
    text-decoration: underline;

    &:active,
    &:hover {
        outline: 0;
    }
}

abbr[title] {
    border-bottom: 1px dotted;
}

img {
    border: 0;
}

kbd,
.#{g.$prefix-cls}-kbd {
    background-color: var(--bnds-g-color-gray-20);
    padding: var(--bnds-g-sizing-2) var(--bnds-g-sizing-6);
    border-radius: .25rem;
    font-size: var(--bnds-g-font-size-12);
    border: 1px solid var(--bnds-g-color-gray-30);
    box-shadow: var(--bnds-g-shadow-6);
}

p,
pre {
    margin: var(--bnds-g-sizing-12) 0;
    padding: 0;
}

br+p,
br+pre {
    margin-top: var(--bnds-g-sizing-0);
}

details,
.#{g.$prefix-cls}-details {
    padding: .25rem 0;
    border-bottom: 1px solid var(--bnds-g-color-gray-30);
    padding-left: 1rem;


    summary {
        margin-left: -1rem;
        margin: .5rem 0rem .5rem -1rem;
        font-weight: 600;
        cursor: pointer;

        &::marker {
            color: var(--bnds-g-color-gray-50);
        }
    }

    &:last-child {
        border-bottom: none;
    }
}

code,
.#{g.$prefix-cls}-code {
    background-color: var(--bnds-g-color-blue-10);
    padding: var(--bnds-g-sizing-2) var(--bnds-g-sizing-6);
    border-radius: 4px;
    color: var(--bnds-g-color-blue-60);
}

del,
.#{g.$prefix-cls}-del {
    text-decoration-color: var(--bnds-g-color-red-50);
}

blockquote,
.#{g.$prefix-cls}-blockquote {
    border-left: 4px solid var(--bnds-g-color-gray-30);
    margin: var(--bnds-g-sizing-16) 0;
    padding: var(--bnds-g-sizing-2);
    padding-left: var(--bnds-g-sizing-16);

    cite {
        font-size: var(--bnds-g-font-size-12);
    }
}

fieldset,
.#{g.$prefix-cls}-fieldset {
    border: 1px solid var(--bnds-g-color-gray-30);
    padding: var(--bnds-g-sizing-8);
    border-radius: 1rem;
    padding: 1rem;

    legend {
        font-weight: 600;
        font-size: 1rem;
        font-weight: 600;
        padding: 0 .5rem;
    }
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    appearance: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

.#{g.$prefix-cls}-table {
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed;

    thead {
        background-color: var(--bnds-g-color-gray-30);
        font-weight: var(--font-weight-semibold);
    }

    tr td {
        padding: .5rem;
    }
}